.aboutContainer {
  display: flex;
  flex-direction: column;
  margin: 0 0.5rem;
}

.emojiContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.emoji {
  margin: 4rem auto 0;
  height: 10rem;
  width: auto;
}

@media only screen and (min-width: 501px) {
  .emoji {
    height: 15rem;
  }
}

@media only screen and (min-width: 1025px) {
  .aboutContainer {
    margin: 0 1.5rem;
  }
}
